export default {
	data() {
		return {
			exam: []
		}
	},
	methods: {
		getExam(){
			//我的考试
			uni.request({
				url:"https://api.bailiban.com/api/admin/miniprogram/my/exam",
				header:{
					"Authorization":uni.getStorageSync("blbUI-token")
				},
				method:"GET",
				dataType:"json",
				success:(res)=> {
					if(res.data.status == 200){
						this.exam = res.data.data
					}else{
						uni.showToast({
							title:'请求接口失败'
						})
					}
				}
			});
		}
	},
	//组件调用后
	created(){
		this.getExam()
	}
}
